const API_URL = process.env.API_URL;
import { find, filter, each, remove } from "lodash";
export const state = () => ({
  list: [],
  current: {},
  tree: []
});

export const mutations = {
  SAVE_DATA(state, data) {
    state.list = data.list;
  },
  SAVE_CURRENT(state, slug) {
    state.current = find(state.list, item => item.slug === slug);
  }
};

export const getters = {
  get(state) {
    return state.list;
  },
  tree(state) {
    return state.tree;
  },
  getCurrent(state) {
    return state.current;
  }
};

export const actions = {
  async fetchAll({ commit }) {
    const data = await this.$axios.$get(`${API_URL}/categories`, {});
    if (data) {
      commit("SAVE_DATA", { list: data });
    }
  },

  async fetchOne({ commit }, params) {
    const data = await this.$axios.$get(`${API_URL}/categories/${params._id}`);
    commit("SAVE_CURRENT", params._id);
  }
};
